.fade-enter{opacity:0.01;pointer-events:none;-webkit-transform:translateY(30px);transform:translateY(30px)}.fade-enter.fade-enter-active{opacity:1;pointer-events:auto;-webkit-transform:translateY(0);transform:translateY(0);transition:all 400ms}.fade-exit{opacity:0;position:fixed;pointer-events:auto}.fade-exit.fade-exit-active{opacity:0.01;pointer-events:none;position:fixed;transition:opacity 400ms}div.transition-group{position:relative}section.route-section{position:absolute;width:100%;top:0;left:0}.body{width:100%;opacity:1;z-index:2}.background{position:fixed;top:0;left:0;height:100%;width:100%;z-index:0;opacity:0.3;background-size:contain;background-repeat:no-repeat;background-position:center center}.landing{background-size:cover;display:flex;position:fixed;flex:1 1 auto;height:100%;width:100%;justify-content:center;align-items:center;flex-direction:column;color:#e0cfc2;font-family:'Space Grotesk', 'DM Sans', Arial, Helvetica, sans-serif}.landing:after{content:'';position:fixed;width:100%;height:100%;z-index:0;background:#191819}.landing .header{width:110%;display:flex;position:relative;word-wrap:none;white-space:nowrap;align-items:center;opacity:0;z-index:1;transition:opacity 1s ease-in-out}.landing .header.left:after,.landing .header.right:before{content:'';display:block;height:10px;flex:1 1 auto;background:#e0cfc2}@media screen and (max-width: 600px){.landing .header.left:after,.landing .header.right:before{height:3px}}.landing h1{font-size:100px;font-weight:600;margin:0;margin:0 32px}@media screen and (max-width: 600px){.landing h1{font-size:36px}}.chevron{position:fixed;left:50%;-webkit-transform:translateX(-50%) rotate(90deg);transform:translateX(-50%) rotate(90deg);bottom:16px;font-size:48px;color:#e0cfc2;font-weight:100;opacity:0;transition:opacity 0.3s ease-out}.blurb{height:100vh;max-width:800px;padding:32px;display:flex;align-items:center;justify-content:center;color:#e0cfc2;flex-direction:column;margin:0 auto}.blurb p.link{margin:0}.blurb a{color:#e0cfc2}.project{height:100vh;width:100vw;display:flex;align-items:center;justify-content:center;color:#191819;flex-direction:row;cursor:pointer}.project img{width:auto;height:60%;z-index:0}@media screen and (max-width: 600px){.project img{height:50%;width:auto}}.project:nth-of-type(odd){flex-direction:row-reverse}@media screen and (max-width: 600px){.project{flex-direction:column}.project:nth-of-type(odd){flex-direction:column}}.project .project-content{max-width:400px;padding:32px;z-index:2}.project .project-content h1{margin:0 0 0 -2px}.project .project-content h4{font-weight:500;font-size:21px;margin:0}.project .project-content h5{text-transform:uppercase;font-size:12px;font-weight:800;margin-bottom:-8px;letter-spacing:1px}.project .project-content h6{font-size:14px;font-weight:500;margin:-8px 0 16px 0}.project .illustration-hero{height:20%;width:auto}@media screen and (max-width: 600px){.project .illustration-hero{height:auto;width:90%}}.project .recall-hero{height:40%;width:auto}@media screen and (max-width: 600px){.project .recall-hero{height:auto;width:90%}}@media screen and (max-width: 600px){.project .fabric-hero.fabric-hero{height:auto;width:120%}}

